import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { FaPhone, FaClock, FaEnvelope, FaCommentDots } from "react-icons/fa";
import "./helpline.css";
import imageLogo  from "../../asesets/banner/BankMudra.jpg";
function MerchantLoginHeader({ loginForm, handleSubmitBack, currentPage }) {
  const [showTooltip, setShowTooltip] = useState(false);
  const navigate = useNavigate();
  const logOut = () => {
    window.localStorage.clear();
    navigate("/login-area");
  };
  return (
    <header className="page-container" >
      <div className="MerchantLoginHeader">
        {" "}
        <div className="container-fluid border-bottom"  >
          <div className="row align-items-center">
            {/* Left: Logo */}
            <div className="col-lg-6 col-md-6 col-6 d-flex align-items-center justify-content-start">
              <div
              >
                <img
                  src={imageLogo}
                  alt="Logo"
                  className="img-fluid"
                  style={{
                    maxWidth: "140px",
                    height: "auto",
                    objectFit: "contain",
                    borderRadius: "8px",
                  }}
                />
              </div>
            </div>

            {/* Right: Buttons */}
            <div className="col-lg-6 col-md-6 col-6 d-flex justify-content-end align-items-center">
              {currentPage === "/registrationComplete" ? (
                <button
                  className="btn btn-danger px-4 fw-semibold"
                  onClick={logOut}
                >
                  LOGOUT
                </button>
              ) : (
                <div className="d-flex">
                  <Link
                    to="/login-area"
                    className="btn btn-outline-primary me-2 px-4 fw-semibold"
                  >
                    LOGIN
                  </Link>
                  <Link
                    to="/Signup"
                    className="btn btn-primary px-4 fw-semibold"
                  >
                    SIGNUP
                  </Link>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}

export default MerchantLoginHeader;
